* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
p,
h2 {
  margin: 0;
  padding: 0;

  list-style: none;
}

.country-list-item,
.country-info-container {
  display: flex;
  align-items: center;

  font-weight: 600;
}

.country-list-item:not(:last-child) {
  margin-bottom: 15px;
}

.country-list-item > img,
.country-info-container > img {
  margin-right: 15px;
  border: 0.1px solid rgb(226, 221, 221);
}

.country-info-container {
  margin-bottom: 10px;
}

.country-info-item {
  font-weight: 600;
}

.country-info-item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info-item > span {
  font-weight: 300;
}
